@import "./src/components/Common/variables";

.wrapper {
  display: flex;
  align-items: flex-start;
  background-color: $mainBackGroundColor;

  .statusBlock {
    margin-left: 1rem;
    width: 100%;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    background-color: $containerColor;
    padding-left: 1rem;
    padding-right: 1rem;
    height: 200px;
    border-radius: 5px;
    .profileName {
      font-size: 19px;
      line-height: 25px;
      font-weight: 400;
      -webkit-font-smoothing: subpixel-antialiased;
      -moz-osx-font-smoothing: auto;
      overflow: hidden;
      text-overflow: ellipsis;

    }

    .profileStatus {
      margin-bottom: 1rem;
      height: 1.5rem;
    }
    .contacts {
      display: flex;
      flex-direction: column;
      width: 300px;
      border: 1px solid;
      background-color: $containerColor;

    }
  }
  .descriptionBlock {
    background-color: $containerColor;
    margin-bottom: 1rem;
    height: 200px;
    border-radius: 5px;
  }
}


